<template>
	<view class="box">
		<view class="title">{{order.Titie}}</view>
		<view class="">
			<image :src="order.Image"></image> <!-- {{order.Image}} -->
		</view>
		<view class="content">{{order.Content}}</view>
		<view class="createTime">日期：{{order.CreateTime}}</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				order: ''
			}
		},
		onLoad() {
			let messageList = uni.getStorageSync("messageList");
			let items = JSON.parse(messageList);
			this.order = items
		}
	}
</script>

<style scoped lang="scss">
	.box {
		padding: 30rpx;
	}

	.title {
		font-size: 34rpx;
		font-weight: 500;
		color: #333333;
		line-height: 48rpx;
		padding-bottom: 30rpx;
	}

	image {
		border-radius: 20rpx;
	}

	.content {
		padding-top: 30rpx;
		font-size: 32rpx;
		font-weight: 400;
		color: #333333;
		line-height: 44rpx;
	}

	.createTime {
		padding-top: 15rpx;
		font-size: 24rpx;
		font-weight: 400;
		color: #999999;
		line-height: 34rpx;
	}
</style>
